.projects {
  max-width: 1100px;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;

  &-item {
    position: relative;
    width: 80%;
    overflow: hidden;
    max-height: 400px;
    border-radius: 5px;
    color: white;
    margin-top: 30px;
    margin-bottom: 10px;
    font-size: 10px;

    &-grow { 
      transition: all .6s ease;  
      width: 91%;
      box-shadow: 0px 0px 20px black; 
    }

    &-shrink { transition: all .6s ease; }
    
    &-img {
      object-fit: cover;
      width: 100%;
    }
    
    &-overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      transition: all .3s ease; 

      &-title {
        font-size: 4em;
        margin-bottom: 10px;
        animation: title-slide-in 1s forwards;
      }

      &-description { animation: description-slide-in 1s forwards; font-size: 1.5em }

      &-resources {
        font-family: $font-rob;
        animation: resources-slide-in 1.3s forwards;
        font-size: 1.5em;
      }

      &-source {
        color: white;
        text-decoration: none;
        position: absolute;
        bottom: 5px;
        right: 5px;
        font-size: 15px;

        &:hover {
          text-decoration: underline;
        }
      }
      
      &:hover { background-color: rgba(0, 0, 0, 0.726); }
    }
  }
}


@keyframes title-slide-in {
  from { transform: translateY(-100px)}
  to { transform: translateY(0px)}
}

@keyframes description-slide-in {
  from { transform: translateY(100px)}
  to { transform: translateY(0px)}
}

@keyframes resources-slide-in {
  from { transform: translateY(130px)}
  to { transform: translateY(0px)}
}

